import Vue from 'vue'
import Router from 'vue-router'
import index from '@/scripts/components/index'
import search from '@/scripts/components/search'
import border from '@/scripts/components/border'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      children: [
                   {
				      path: '/search',
				      name: 'search',
				      component: search
		    		},
		    		{
				      path: '/border',
				      name: 'border',
				      component: border
		    		}
     ]
    }

  ]
})



